.home {
    &__hero {
        padding-top: 7.5rem;
        text-align: center;
        row-gap: 0;
        
    }
    
}
.about {
    &__button {
        width: max-content;
        color: var(--title-cl);
        font-size: var(--small-fs);
        font-family: mssb;
        display: flex;
        align-items: center;
        column-gap: 0.5rem;
        &-icon {
            width: 1rem;
            height: 1rem;
            path {
                fill: var(--text-cl);
            }
        }
        &:hover {
            text-decoration: underline;
        }
    }
    &__social {
        display: flex;
        column-gap: 1rem;
        &-link {
            color: var(--title-cl);
            font-family: mssb;
            font-size: var(--small-fs);
        }
    }
}
.skills {
    &__list,
    &__content {
        display: grid;
    }
    &__content {
        grid-template-columns: repeat(2, max-content);
        align-items: baseline;
        column-gap: 3rem;
    }
    &__list {
        row-gap: 1rem;
        li {
            margin: 0;
        }
    }
}
